@mixin h-4 {
    line-height: 1.5;
    font-family: 'Cormorant Garamond', serif;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
}

@mixin h-5 {
    line-height: 1.5;
    font-family: 'Poppins', Helvetica, Arial, sans-serif;
    font-size: 24px;
    font-style: italic; 
    font-weight: 500;
}

@mixin h-6 {
    line-height: 1.2;
    font-family: 'Poppins', Helvetica, Arial, sans-serif;
    font-weight: 500;
    text-transform: uppercase;
