}

@mixin p-text {
    font-family: 'Poppins', Helvetica, Arial, sans-serif;
    font-size: 18px; 
    font-style: normal; 
    font-weight: 300;
}

@mixin p-bold {
    @include p-text;
    font-weight: 500;
}

@mixin quote {
    font-family: 'Cormorant Garamond', serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
}

@mixin caption {
    font-family: 'Poppins', Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
}